import { Directive, EventEmitter, Input, Output} from '@angular/core';
import { MatAutocomplete} from '@angular/material';
import { takeUntil, tap} from 'rxjs/operators';
import { Subject } from 'rxjs';


export interface IAutoCompleteScrollEvent {
  autoComplete: MatAutocomplete;
  scrollEvent: Event;
}


@Directive({
  selector: 'mat-autocomplete[optionsScroll]'
})
export class OptionsScrollDirective  {

  /**
   * This value would different depends of styles
   */
  @Input() thresholdPercent = .9;
  @Output('optionsScroll') scroll = new EventEmitter<IAutoCompleteScrollEvent>();
  _onDestroy = new Subject();
    

  constructor(public autoComplete: MatAutocomplete) {
    this.autoComplete.opened.pipe(
      tap(() => {
        // Note: When autocomplete raises opened, panel is not yet created (by Overlay)
        // Note: The panel will be available on next tick
        // Note: The panel wil NOT open if there are no options to display
        setTimeout(() => {
          // Note: remove listner just for safety, in case the close event is skipped.
          this.removeScrollEventListener();
          if(!!this.autoComplete &&
            !!this.autoComplete.panel &&
            !!this.autoComplete.panel.nativeElement){
              this.autoComplete.panel.nativeElement
                .addEventListener('scroll', this.onScroll.bind(this),false)
            }
        });
      }),
      takeUntil(this._onDestroy)).subscribe();

    this.autoComplete.closed.pipe(
      tap(() => this.removeScrollEventListener()),
      takeUntil(this._onDestroy)).subscribe();
  }

  private removeScrollEventListener() {
    if(!!this.autoComplete &&
      !!this.autoComplete.panel &&
      !!this.autoComplete.panel.nativeElement){
    this.autoComplete.panel.nativeElement
      .removeEventListener('scroll', this.onScroll);
    }
  }

  ngOnDestroy() {
    this._onDestroy.next();
    this._onDestroy.complete();

    this.removeScrollEventListener();
  }

  lastScrollTop  = 0;
  onScroll(event: any) {

    var st = event.target.pageYOffset || event.target.scrollTop; // Credits: how to know if it's down or up scroll "https://github.com/qeremy/so/blob/master/so.dom.js#L426"
    if (st > this.lastScrollTop){
       // downscroll code       
       if (this.thresholdPercent === undefined) {
        this.scroll.next({ autoComplete: this.autoComplete, scrollEvent: event });
      } else {                    
        
        const threshold = this.thresholdPercent * 100 * event.target.scrollHeight / 100;
        const current = event.target.scrollTop + event.target.clientHeight;
        //console.log(`scroll ${current}, threshold: ${threshold}`)     
        if (current > threshold) { 
          //console.log('load next page');        
          this.scroll.next({ autoComplete: this.autoComplete, scrollEvent: event });
        }                  
      }
    } else {
       // upscroll code       
    }
    this.lastScrollTop = st <= 0 ? 0 : st;  
  }
  
}