import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewChild, ElementRef } from '@angular/core';
import { MatSort, MatTable, MatTableDataSource } from '@angular/material';
import { Sort } from '@angular/material/typings/sort';

import {
  AddRowButton,
  Cell,
  ChangeColumnConfigurationType,
  ColumnConfig,
  ColumnConfigUtil,
  ConfigCellStyles,
  ConfigRowStyles,
  DropElement,
  EventColumn,
  EventScope,
  EventSearch,
  RequestTableHelisa,
  SelectObject,
  TableHelisaType,
  TotalGroup,
  TotalType
} from './table-helisa.interface';
import { TableHelisaService } from './table-helisa.service';
import { TableHelisaConnectComponent } from './table-helisa-connect.component';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import {of} from 'rxjs';

interface RowData {
  data: any;
  rowType: RowType;
}

enum RowType {
  GROUP_TITLE, GROUP_FOOTER, ROW
}



@Component({
  selector: 'hel-table',
  templateUrl: './table-helisa.component.html',
  styleUrls: ['./table-helisa.component.sass']
})
export class TableHelisaComponent<T> implements OnInit, AfterViewInit {

  private tableHelisaConnectComponent: TableHelisaConnectComponent<T>;
  totalData: Array<number>;
  rawData: Array<T>;
  data: MatTableDataSource<RowData>;
  displayedColumns: string[] = [];
  displayedColumnsWithTitle: string[] = [];
  displayedColumnsWithSubtitle: string[] = [];
  displayedColumnsWithFooter: string[] = [];
  columnConfig: Array<ColumnConfig>;
  selectedObject: T;
  lastSearch: string;
  type: TableHelisaType = TableHelisaType.LOCAL;
  indexRowSelect: number;
  private scrollCount: number = 0;
  hasSubtitle = false;
  private indexRowStartDrag = -1;
  private lastIndexRowDrag = -1;
  private dataBeforeDrag = null;

  @ViewChild(MatSort) matSort: MatSort;
  @ViewChild(MatTable) matTable: MatTable<any>;
  @ViewChild(MatTable,{read:ElementRef}) matTableElement: ElementRef;
  @ViewChild("containerTable") containerTable:ElementRef;

  @Output() sort: EventEmitter<EventColumn> = new EventEmitter<EventColumn>();
  @Output() total: EventEmitter<EventColumn> = new EventEmitter<EventColumn>();
  @Output() search: EventEmitter<EventSearch> = new EventEmitter<EventSearch>();

  /**
   * Deprecado, cambiar por electObject
   */
  @Output() select: EventEmitter<T> = new EventEmitter<T>();
  @Output() selectCell = new EventEmitter<Cell>();
  @Output() selectObject: EventEmitter<SelectObject<T>> = new EventEmitter<SelectObject<T>>();
  @Output() nextPage: EventEmitter<RequestTableHelisa> = new EventEmitter<RequestTableHelisa>();
  @Input() showTitle = true;
  @Input() isCellSelection = false;
  @Input() count: number;
  @Input() configCellStyles: Array<ConfigCellStyles>;
  @Input() configRowStylesFromColumn: Array<ConfigRowStyles>;
  @Input() selectedCells: Cell;
  @Output() drop: EventEmitter<DropElement<T>> = new EventEmitter<DropElement<T>>();
  @Input() isDragged = false;
  @Input() addRowButton: AddRowButton = { showButton: false, text: "" };
  @Output() addRow: EventEmitter<void> = new EventEmitter<void>();
  @Output() bookClicked: EventEmitter<T> = new EventEmitter<T>();
  @Input() addBookButton: boolean = false;
  @Input() showToolTip: boolean = true;
  showFooter = false;
  showSearch = false;



  /**
     * Tiempo antes de ocultarla el mensaje del tooltip
     */
    @Input('hideDelay') hideDelay: number = 600;

    /**
     * Tiempo antes de mostra el mensaje del tooltip
     */
    @Input('showDelay') showDelay: number = 500;
   

  constructor(private tableService: TableHelisaService<T>) { }

  ngOnInit() {
    this.tableService.nextPageReturn.subscribe(
      data => {
        if (!data.table || data.table === this) {
          this.receivePage(data.obj);
        }
      }
    );
    this.tableService.totalReturn.subscribe(info => {
      if (info) {
        this.columnConfig.forEach((column, idx) => {
          if (column === info.obj.column) {
            this.totalData[idx] = this.getGroupValue(column, { sum: info.obj.value, count: this.count });
          }
        });
      }
    });
    this.matSort.sortChange.subscribe(
      (event: Sort) => {
        const column: ColumnConfig = this.columnConfig.find(c => c.name === event.active);
        column.sortDirection = event.direction;
        this.sort.emit({ column, columnConfigurations: this.columnConfig, type: ChangeColumnConfigurationType.SORT });
      }
    );

    this.tableService.emitVisibleButton.subscribe(
      data => {
        if (data != undefined && data != null) {
          this.addRowButton.showButton = data;
        }

      }
    )
  }

  ngAfterViewInit() {
    if (this.isCellSelection) {
      this.matTable.renderRows();
    }
  }

  @Input()
  set isRemote(w: boolean) {
    this.type = w ? TableHelisaType.REMOTE : TableHelisaType.LOCAL;
    this.tableHelisaConnectComponent = new TableHelisaConnectComponent<T>();
    if (this.type === TableHelisaType.REMOTE) {
      this.goNextPage();
    } else {
      this.tableHelisaConnectComponent.page++;
    }
  }

  @Input()
  set columnConfiguration(columnConfiguration: Array<ColumnConfig>) {
    this.hasSubtitle = false;
    this.columnConfig = columnConfiguration;
    this.displayedColumns.splice(0, this.displayedColumns.length);
    if (columnConfiguration) {
      columnConfiguration.forEach(column => {
        if (column.visible) {
          this.displayedColumns.push(column.name);
        }
        if (!this.hasSubtitle) {
          this.hasSubtitle = column.subtitle != undefined;
        }
      });
      if (this.rawData) {
        this.dataSource = this.rawData;
      }
    }
    this.displayedColumnsWithTitle.splice(0, this.displayedColumnsWithTitle.length);
    this.displayedColumnsWithSubtitle.splice(0, this.displayedColumnsWithSubtitle.length);
    this.displayedColumnsWithFooter.splice(0, this.displayedColumnsWithFooter.length);
    this.getColumnsWithTitle().forEach(col => this.displayedColumnsWithTitle.push(col));
    this.getHeaderSubtitle().forEach(col => this.displayedColumnsWithSubtitle.push(col));
    this.footerDisplayedColumns().forEach(col => this.displayedColumnsWithFooter.push(col));
  }

  private _dataSource: Array<any> = [];

  @Input()
  set dataSource(dataSource: Array<any>) {
    this._dataSource = dataSource;
    this.rawData = dataSource;
    if (this.rawData) { this.prepareDataSource(); }
  }

  get dataSource():Array<any>{
    return this._dataSource;
  }

  @Input()
  set selectedIndexRow(idRowSelected: number) {
    this.indexRowSelect = idRowSelected;
    if (this.rawData && this.rawData.length) {
      if ((idRowSelected >= this.rawData.length || idRowSelected < 0)) {
        this.indexRowSelect = 0;
      }
      this.selectRow({ data: this.rawData[this.indexRowSelect], rowType: RowType.ROW }, false);
    }
  }

  private prepareDataSource() {
    const changeData = Array<RowData>();
    let haveGroup = false;
    let groupFooter: Array<TotalGroup>;
    this.columnConfig.forEach(column => {
      if (column.totalType !== undefined && (this.type === TableHelisaType.LOCAL || this.tableHelisaConnectComponent.page <= 1)) {
        this.totalData = new Array<number>(this.columnConfig.length);
        this.showFooter = true;
        this.total.emit({ column, columnConfigurations: this.columnConfig, type: ChangeColumnConfigurationType.TOTAL });
      }
      this.showSearch = this.showSearch || column.searchable;
      haveGroup = haveGroup || column.groupable;
    });
    if (haveGroup) {
      this.rawData = this.rawData.sort((a, b) => {
        let result = 0;
        this.columnConfig.forEach(column => {
          if (result === 0) {
            result = this.compare(a, b);
          }
        });
        return result;
      });
    }
    this.rawData.forEach(row => {
      if (haveGroup && (changeData.length === 0 || this.compare(changeData[changeData.length - 1].data, row) !== 0)) {
        if (groupFooter) {
          changeData.push({ data: groupFooter, rowType: RowType.GROUP_FOOTER });
        }
        changeData.push({ data: row, rowType: RowType.GROUP_TITLE });
        groupFooter = new Array<TotalGroup>(this.columnConfig.length);
      }
      if (haveGroup) { this.addTotalGroup(groupFooter, row); }
      changeData.push({ data: row, rowType: RowType.ROW });
    });
    this.data = new MatTableDataSource<RowData>(changeData);
    if (this.rawData && this.rawData.length && this.indexRowSelect && !this.selectedObject) {
      if (this.indexRowSelect >= this.rawData.length || this.indexRowSelect < 0)
        this.indexRowSelect = 0;
      this.selectRow({ data: this.rawData[this.indexRowSelect], rowType: RowType.ROW }, false);
    }
  }

  private addTotalGroup(rowTotal: Array<TotalGroup>, row: any) {
    this.columnConfig.forEach((column, index) => {
      if (column.totalType !== undefined) {
        if (rowTotal[index] === undefined) {
          rowTotal[index] = { sum: ColumnConfigUtil.getValue(row, column), count: 1 };
        } else {
          rowTotal[index].sum += ColumnConfigUtil.getValue(row, column);
          rowTotal[index].count++;
        }
      }
    });
  }

  private compare(a: any, b: any): number {
    let ws = 0;
    this.columnConfig.forEach(column => {
      if (ws === 0 && column.groupable) {
        if (ColumnConfigUtil.getValue(a, column) < ColumnConfigUtil.getValue(b, column)) { ws = -1; } else if (ColumnConfigUtil.getValue(a, column) > ColumnConfigUtil.getValue(b, column)) { ws = 1; }
      }
    });
    return ws;
  }

  getGroupDescription(obj: any): string {
    let result = '';
    this.columnConfig.forEach(column => {
      if (column.groupable) {
        result += (result.length ? ' - ' : '') + ColumnConfigUtil.getValue(obj, column);
      }
    });
    return result;
  }

  isGroupTitle(index, item): boolean {
    return item.rowType === RowType.GROUP_TITLE;
  }

  isRow(index, item): boolean {
    return item.rowType === RowType.ROW;
  }

  isGroupFooter(index, item): boolean {
    return item.rowType === RowType.GROUP_FOOTER;
  }

  footerDisplayedColumns(): Array<string> {
    return this.displayedColumns.map(name => 'footer-' + name);
  }

  getGroupValue(column: ColumnConfig, data: TotalGroup): number {
    if (column.totalType === TotalType.SUM) { return data.sum; }
    if (column.totalType === TotalType.COUNT) { return data.count; }
    if (column.totalType === TotalType.AVERAGE) { return 1. * data.sum / data.count; }
    return undefined;
  }

  getValue(obj: any, column: ColumnConfig) {
    return ColumnConfigUtil.getValue(obj, column);
  }

  getValueTooltip(obj: any, column: ColumnConfig) {
    if (this.showToolTip) {
      return ColumnConfigUtil.getValue(obj, column);
    } else { return null }
  }

  searchText(text) {
    this.lastSearch = text;
    this.search.emit({ text, columnConfigurations: this.columnConfig });
  }

  selectRow(row, isUser) {
    this.selectedObject = row.data;
    this.select.emit(this.selectedObject);
    this.selectObject.emit({ value: this.selectedObject, scope: isUser ? EventScope.USER : EventScope.CODE_CALL });
  }

  onScroll(event) {
    const element: HTMLDivElement = event.target;
    if (element.scrollHeight - element.scrollTop < 1000) {
      this.goNextPage();
    }
  }

  private goNextPage() {
    if (!this.tableHelisaConnectComponent.isLastPage && !this.tableHelisaConnectComponent.isUsed) {
      this.tableHelisaConnectComponent.isUsed = true;
      this.nextPage.emit({
        page: this.tableHelisaConnectComponent.nextPage(),
        body: this.tableHelisaConnectComponent.getBody(this.columnConfig, this.lastSearch)
      });
    }
  }

  private receivePage(data: T[]) {
    if (!this.rawData) {
      this.rawData = new Array<T>();
    }
    this.rawData = this.rawData.concat(data);
    this.dataSource = this.rawData;
    this.tableHelisaConnectComponent.isLastPage = data.length === 0;
    this.tableHelisaConnectComponent.isUsed = false;
  }

  dblClickCell() {
    this.selectCell.emit(this.selectedCells);
  }

  selectedCell(element, column: ColumnConfig) {
    this.selectedCells = { column: column, row: element };
    this.selectCell.emit(this.selectedCells);
  }

  isSelectedCell(row, column: ColumnConfig): boolean {
    if (this.isCellSelection) {
      if (this.selectedCells != null) {
        if (this.selectedCells.column.name === column.name &&
          this.selectedCells.row.data === row.data) {
          return true;
        }
      }
    }
    return false;
  }

  getClassToCell(row, column: ColumnConfig) {
    let classToCell = new Array<string>();
    if (this.configCellStyles) {
      let found = this.configCellStyles.find(c => {
        return c.cellData === this.getValue(row, column);
      });
      if (found) {
        classToCell.push(found.classCell);
      }
    }
    if (column.columnStyle)
      classToCell.push(column.columnStyle);
    return classToCell;
  }

  getClassToRow(row) {
    const classToRow = new Array<string>();
    if(row === this.selectedObject && !this.isCellSelection)
      classToRow.push('');
    if (this.configRowStylesFromColumn) {
      let found = this.configRowStylesFromColumn.find(c => {
        return c.data === this.getValue(row, c.column);
      });
      if (found) {
        classToRow.push(found.classRow);
      }
    }
    return classToRow;
  }

  onDrop(event) {
    if(this.isDragged && this.indexRowStartDrag >= 0) {
      const rowIndex = this.getRowIndex(event.pageY);
      const array: RowData[] = this.dataBeforeDrag.data;
      const rawData = this.rawData;
      moveItemInArray(array, this.indexRowStartDrag, rowIndex);
      moveItemInArray(rawData, this.indexRowStartDrag, rowIndex);
      this.drop.emit({ value: array[rowIndex].data, order: rowIndex });
      this.rawData = rawData;
      this.data = new MatTableDataSource(array);
      event.stopPropagation();
    }
  }

  tableKeydown(event: KeyboardEvent) {
    if (!this.isCellSelection) {
      let currentIndex = this.data.data.findIndex(row => row.data === this.selectedObject);
      let newSelection = -10;
      if (event.key === 'ArrowDown') {
        this.scrollCount++;
        this.data.data.forEach((row, index) => {
          if (newSelection == -10 && index > currentIndex && row.rowType == RowType.ROW)
            newSelection = index;
        });
      }
      if (event.key === 'ArrowUp') {
        this.scrollCount--;
        currentIndex = this.data.data.length - currentIndex - 1;
        this.data.data.reverse().forEach((row, index) => {
          if (newSelection == -10 && index > currentIndex && row.rowType == RowType.ROW)
            newSelection = index;
        });
        this.data.data.reverse();
        if (newSelection != -10) {
          newSelection = this.data.data.length - newSelection - 1;
        }
      }
      if (newSelection != -10) {
        this.selectRow(this.data.data[newSelection], true);
      }
      if (Math.abs(this.scrollCount) >= 2)
        this.scrollCount = 0;
      else
        event.preventDefault();
    }
  }

  /**
   * Emite el evento cuando se da click al boton AddRow
   */
  onAddRow() {
    this.addRow.emit();
  }

  onBookClicked(selectedObject) {
    this.bookClicked.emit(selectedObject);
  }

  getHeaderSubtitle(): string[] {
    let x = this.columnConfig.map((column, index) => {
      if (column.visible && column.subtitle != undefined)
        return 'subtitle' + index;
      else
        return null;
    }).filter(data => data != null);
    return x;
  }

  getColumnsWithTitle(){
    return this.columnConfig.filter(column => 
        column.visible && column.title != undefined
    ).map(col => col.name);
  }

  dragger(event) {
    if (this.isDragged && this.indexRowStartDrag >= 0) {
      const rowIndex = this.getRowIndex(event.pageY);
      if (rowIndex != this.lastIndexRowDrag) {
        this.lastIndexRowDrag = rowIndex;
        //This can have a memory problem with big data
        const array: RowData[] = [...this.dataBeforeDrag.data];
        moveItemInArray(array, this.indexRowStartDrag, rowIndex);
        this.data = new MatTableDataSource(array);
      }
      event.preventDefault();
      return true;
    }
  }

  startDrag(event) {
    this.indexRowStartDrag = this.getRowIndex(event.pageY);
    this.lastIndexRowDrag = this.indexRowStartDrag;
    this.dataBeforeDrag = this.data;
  }

  private getRowIndex(pageY) {
    let offsetTop = 0;
    let container = this.containerTable.nativeElement;
    while ((container != null) && (offsetTop == 0)) {
      offsetTop = container.offsetTop;
      container = container.parentElement;
    }
    let rowIndex = -1;
    const rows: HTMLCollection = this.matTableElement.nativeElement.children[1].children;
    for (let i = 0; i < rows.length; i++){
      const row: HTMLElement = (rows[i] as HTMLElement);
      if (pageY - offsetTop > row.offsetTop - this.containerTable.nativeElement.scrollTop)
        rowIndex = i;
    }
    if (rowIndex < 0) rowIndex = 0;
    return rowIndex;
  }

}
